.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
}

.header {
    font-size: calc(5px + 2vmin);
    display: flex;
    text-align: center;
    /* padding: 10 10 10 10 10; */
    background-color: #36383b;
    justify-content: center;
    color: whitesmoke;
    border-bottom: 50px;
}

.header-item {
    /* width: 200px;
    height: 100px; */
    margin: auto;
    position: relative;
    line-height: 1.3;
    padding: .6em 1.4em .5em .8em;
    justify-content: center;
}

.header-left {
    float: left;
    width: 200px;
    justify-content: left;
}

.main-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  top: 10%;
}


.game-list {
    color: white;
}

.about-message {
    font-size: smaller;
}

.game-item {
    background-color: #36383b;
    border: 2px solid #36383b;
    padding: 5px 5px 5px 5px;
    margin: 10px 75px 10px 75px;
    width: 400px;
    border-radius: .5em;
    border-left: 20px;
    border-right: 20px;
}

a {
    color: white;
    text-decoration: none;
}


.explainer {
    font-size: smaller;
}


/* class applies to select element itself, not a wrapper element */
select {
    display: block;
    font-size: 16px;
    font-family: sans-serif;
    font-weight: 700;
    color: #36383b;
    
    width: 100%;
    max-width: 100%; /* useful when width is set to anything other than 100% */
    box-sizing: border-box;
    margin: 0;
    border: 2px solid #36383b;
    /* box-shadow: 0 1px 0 1px rgba(0,0,0,.04); */
    border-radius: .5em;
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    background-color: #36383b;
    /* note: bg image below uses 2 urls. The first is an svg data uri for the arrow icon, and the second is the gradient. 
      for the icon, if you want to change the color, be sure to use `%23` instead of `#`, since it's a url. You can also swap in a different svg icon or an external image reference
      
    */
    background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23007CB2%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E'),
      linear-gradient(to bottom, #ffffff 0%,#e5e5e5 100%);
    background-repeat: no-repeat, repeat;
    /* arrow icon position (1em from the right, 50% vertical) , then gradient position*/
    background-position: right .7em top 50%, 0 0;
    /* icon size, then gradient */
    background-size: .65em auto, 100%;
}

.showError{
    color: red;
    font-size: 12px;
}

.hideError{
    display: none;
    
}

.gameAnswers{
    /* padding: 20px; */
    align-items: center;
}

.accepted {
    background-color: white;
    color: #36383b
}


/* picture guess */

.pictureGuess {
    display:flex;
    justify-content: flex-start;
}

/* left  */

.leftBar{
    border-radius: 15px;
    border: 2px white;
    border-style: solid;
    justify-content: space-between;
    flex-direction: column;
}

.pictureTimer {
    border-radius: 15px;
    border: 2px white;
    border-style: solid;
    align-self: flex-start;
}


.currentTurn {
    border-radius: 15px;
    border: 2px white;
    border-style: solid;
    align-self: flex-end;
}

/* center  */

.drawingPanel {
    flex-grow: 2;
    border: 1px solid;
    background-color: white;
    padding: 2px;
    margin-left: 20px;
    margin-right: 20px;
    border-radius: 2px;
}

/* right  */

.hideJoin{
    display: none;
}

.rightBar {
    border-radius: 15px;
    border: 2px white;
    border-style: solid;
}

.currentPlayers {
    border-radius: 15px;
    border: 2px white;
    border-style: solid;
}

.guesses {
    border-radius: 15px;
    border: 2px white;
    border-style: solid;
}

.wordGridTable {
    margin-left: auto;
    margin-right: auto;
    cursor: pointer;
    user-select:none;
}

.waitingTable {
    margin-left: auto;
    margin-right: auto;
    cursor: pointer;
    user-select:none;
}

.wordGridTable td {
    border: 1px white solid;
    margin-left: auto;
    margin-right: auto;
    padding: 20px
}
.waitingTable td {
    border: 1px white solid;
    margin-left: auto;
    margin-right: auto;
    padding: 20px
}

.highlight {
    background-color: white;
    color: #282c34;
}

.wordGridDictionaryTable {
    margin-left: auto;
    margin-right: auto;
    cursor: pointer;
    user-select:none;
}

.wordGridDictionaryTable tr:hover{
    background-color: white;
    color: #282c34;
}



.playerAnswers {
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 10px;
    cursor: pointer;
    user-select:none;
}

.playerAnswers td{
    padding:15px;
}


.seenWord {
    text-decoration: line-through;
}


 /* wordgrid solver */

.wordGridSolver {
    padding-top: 10px;
    padding-bottom: 10px;
}